import "core-js/modules/es.array.includes.js";
import { ref, defineComponent, toRef, onMounted } from 'vue';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faArrowPointer, faAngleRight, faCheck } from '@fortawesome/free-solid-svg-icons';
import { versionCompare } from '@/utils/versionCompare';
import DlUpdate from '@/utils/dlUpdate';
const latest = new DlUpdate('frostflake');
library.add(faArrowPointer, faAngleRight, faCheck);
export default defineComponent({
  props: {
    control: {
      type: Object,
      required: true
    }
  },
  emits: ['done'],

  setup(props, {
    emit
  }) {
    const w = toRef(props, 'control');
    const loading = ref(true);
    const notFound = ref(false);
    const denied = ref(false);
    const gameNotFound = ref(false);
    const needUpdate = ref(false);
    const version = ref('');

    const enable = async (force = false) => {
      if (loading.value && !force) return;
      loading.value = true;
      denied.value = false;
      const alive = await w.value.check();

      if (!alive) {
        notFound.value = true;
        loading.value = false;
        needUpdate.value = false;
        w.value.launch();
        return;
      } else if (versionCompare(w.value.version, process.env.VUE_APP_CLIENT_MIN_VER || '') < 0) {
        needUpdate.value = true;
        notFound.value = true;
        loading.value = false;
        version.value = w.value.version;
        return;
      } else {
        needUpdate.value = false;

        try {
          loading.value = true;
          const authorized = await w.value.authorize();

          if (authorized) {
            let windows = await w.value.listWindows();
            windows = windows.filter(w => (w.title.includes('原神') || w.title.toLowerCase().includes('genshin')) && w.classname === 'UnityWndClass');

            if (windows.length === 0) {
              notFound.value = false;
              loading.value = false;
              gameNotFound.value = true;
              return;
            }

            emit('done', windows[0].hWnd);
          } else {
            denied.value = true;
          }

          notFound.value = false;
        } catch (e) {
          notFound.value = true;
          console.log(e);
        }
      }

      loading.value = false;
    };

    onMounted(() => enable(true));
    return {
      loading,
      notFound,
      enable,
      denied,
      gameNotFound,
      needUpdate,
      version,
      cVersion: latest.version,
      cSize: latest.formattedSize
    };
  }

});