import { defineComponent, ref, onBeforeUnmount, provide, onMounted } from 'vue-demi';
export default defineComponent({
  props: {
    width: {
      type: Number,
      required: true
    },
    height: {
      type: Number,
      required: true
    }
  },
  emits: ['exit'],

  setup() {
    let hasStream = false;
    const video = ref(null);
    const canvas = ref(null);
    let canvasStream;
    provide('canvas', canvas);

    const init = async () => {
      if (!hasStream) {
        canvasStream = canvas.value.captureStream();
        video.value.srcObject = canvasStream;
        hasStream = true;
      }

      if (document.pictureInPictureElement === null) {
        try {
          await video.value.play();
          await video.value.requestPictureInPicture();
        } catch (e) {
          document.addEventListener('mousedown', async () => {
            await video.value.play();
            await video.value.requestPictureInPicture();
          }, {
            once: true
          });
        }
      }
    };

    onMounted(init);
    onBeforeUnmount(() => {
      if (video.value) {
        video.value.srcObject = null;
      }

      if (canvasStream) {
        canvasStream.getTracks().forEach(track => track.stop());
      }

      try {
        if (document.pictureInPictureElement === video.value) {
          document.exitPictureInPicture();
        }
      } catch (e) {}
    });
    return {
      init,
      video,
      canvas
    };
  }

});